/* General Styles */
body {
  font-family: Arial, sans-serif;
  background: #E0F2FE; /* Lighter Blue */
  color: #111827; /* Neutral Blue */
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  overflow-x: hidden; /* Prevent horizontal overflow */
}

/* Banner Section */
.banner-container {
  position: relative;
  width: 100vw; /* Full width of the viewport */
  height: 100vh; /* Full height of the viewport */
  overflow: hidden;
  cursor: none; /* Hide the default cursor */
  background: #000000; /* Black background to cover any white spaces */
}

.banner-image {
  position: absolute;
  top: -20px; /* Adjust the top position */
  left: -20px; /* Adjust the left position */
  width: 105%; /* Adjust the width to ensure it covers more area */
  height: 105%; /* Adjust the height to ensure it covers more area */
  object-fit: cover;
  transform-origin: center; /* Ensure the image is centered */
}

/* Adjustments for smaller screens (iPhone sizes) */
@media (max-width: 768px) {
  .banner-image {
    width: 120%; /* Expand the image width on mobile devices */
    height: 120%; /* Expand the image height on mobile devices */
    left: -10%; /* Adjust the position to center the image */
    top: -10%; /* Adjust the position to center the image */
  }

  .project-image-2,
  .project-image-1 {
    width: 90%; /* Increase image size for smaller screens */
  }
}

.banner-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
}

.banner-name {
  font-size: 4em; /* Increased Font Size */
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3); /* Adjust values as needed */
}

.banner-description {
  font-size: 2em; /* Increased Font Size */
  margin-top: 10px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2); /* Adjust values as needed */
}

/* Custom Cursor */
.custom-cursor {
  position: absolute;
  width: 20px; /* Smaller size */
  height: 20px; /* Smaller size */
  border-radius: 50%;
  background: radial-gradient(circle, white 0%, blue 100%); /* Gradient from white to blue */
  pointer-events: none;
  transition: transform 0.1s ease, background-color 0.3s ease;
  animation: cursorAnimation 1s infinite;
}

.custom-cursor::before {
  content: '';
  position: absolute;
  top: -5px; /* Adjusted for smaller outer ring */
  left: -5px; /* Adjusted for smaller outer ring */
  width: 30px; /* Smaller size */
  height: 30px; /* Smaller size */
  border-radius: 50%;
  border: 2px solid rgba(60, 120, 240, 0.7); /* Outer ring */
  box-sizing: border-box;
  animation: outerRingAnimation 1s infinite;
}

@keyframes cursorAnimation {
  0%, 100% {
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
  }
}

@keyframes outerRingAnimation {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2); /* Less expansion */
    opacity: 0.5;
  }
}

.banner-link {
  color: #60A5FA; /* Accent Blue */
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.banner-link:hover {
  color: #93C5FD; /* Light Blue */
}

/* Section Styling */
.section-container {
  display: flex;
  flex-direction: column; /* Stack sections vertically */
  align-items: center; /* Center-align items */
  padding: 20px;
  margin: 20px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.section-container:hover {
  transform: translate(10px, 10px); /* Move down to the right on hover */
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15); /* Add a shadow effect on hover */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for the hover effect */
}

.section-image {
  width: 100%;
  max-width: 150px;
  border-radius: 8px;
  margin-bottom: 20px; /* Add margin below image for spacing */
}

.section-text {
  flex-grow: 1;
  max-width: 100%;
  text-align: center; /* Center-align text */
}

.section-text p {
  line-height: 1.6;
}

/* Title Styling */
.section-text h2,
.section-text h3 {
  color: #1E3A8A; /* Primary Blue */
  font-weight: bold;
  text-align: center; /* Center-align titles */
  margin-bottom: 10px; /* Add space below titles */
}

/* Specific Styles for Projects Section */
.projects-section {
  display: flex;
  flex-direction: column; /* Stack projects vertically */
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project-image-2 {
  width: 45%; /* Set width for better responsiveness */
  height: auto; /* Ensure the height adjusts proportionally */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(60, 120, 240, 0.6); /* Default blue glow */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for hover effect */
}

.project-image-2:hover {
  transform: scale(1.01); /* Slightly enlarge image on hover */
  box-shadow: 0 0 20px rgba(60, 120, 240, 0.8), 0 0 30px rgba(60, 120, 240, 0.6); /* Intensified blue glow */
}

/* Project Image 1 Styling */
.project-image-1 {
  width: 70%; /* Set the width for better responsiveness */
  height: auto; /* Ensure the height adjusts proportionally */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(60, 120, 240, 0.6); /* Default blue glow */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for hover effect */
}

.project-image-1:hover {
  transform: scale(1.01); /* Slightly enlarge image on hover */
  box-shadow: 0 0 20px rgba(60, 120, 240, 0.8), 0 0 30px rgba(60, 120, 240, 0.6); /* Intensified blue glow */
}

.project-image-2,
  .project-image-1 {
    margin-top: 10px;
    margin-bottom: 10px;
}

/* Responsive Design */
@media (min-width: 769px) {
  .section-container {
    flex-direction: row; /* Align sections horizontally for larger screens */
  }

  .section-image {
    margin-right: 20px; /* Add margin to the right for spacing */
    margin-bottom: 0; /* Remove bottom margin */
  }

  .section-text {
    text-align: left; /* Align text to the left */
  }

  .section-text h2,
  .section-text h3 {
    text-align: left; /* Align titles to the left */
  }

  .project-text {
    margin-right: 50px;
    margin-top: -15px;
  }

  .project-text2 {
    margin-top: 30px;
  }
}

/* Adjustments for smaller screens (iPhone sizes) */
@media (max-width: 768px) {
  .project-image-2,
  .project-image-1 {
    width: 90%; /* Increase image size for smaller screens */
  }
}
